import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Film } from 'src/app/services/interfaces/film';
import { DataFilmService } from 'src/app/services/data-film.service';
import { NavController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth.service';

@Component({
	selector: 'app-info-add-film',
	templateUrl: './info-add-film.page.html',
	styleUrls: [ './info-add-film.page.scss' ]
})
export class InfoAddFilmPage implements OnInit {
	public selectedFilm: Film;

	constructor(
		private navCtrl: NavController,
		private activatedRoute: ActivatedRoute,
		private serviceFilm: DataFilmService,
		private authService: AuthService
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idFilm')) {
				this.navCtrl.navigateBack([ '/selectionner-film' ]);
				return;
			}
			this.selectedFilm = { ...this.serviceFilm.getInAllFilm(paramMap.get('idFilm')) };
		});
		this.selectedFilm.note = 2;
	}

	goBack() {
		this.navCtrl.navigateBack([ '/selectionner-film' ]);
	}

	Valider() {
		this.serviceFilm.addInMyFilm(this.selectedFilm, this.authService.userId);
		this.navCtrl.navigateBack([ '/medias/films' ]);
	}

	passerNote(note: number) {
		this.selectedFilm.note = note;
	}
}
